nav {
  width: 100%;
  clear: both;
}

.risq-nav {
  background: #000;
  float: right;
  width: 680px;
  height: 680px;
  border-radius: 100% 0 100% 100%;
  right: 0px;
}

.risq-nav h2 {
  color: #fff;
}

.risq-nav a {
  color: #fff;
  text-decoration: none;
}

.risq-nav a :hover {
  color: #fff;
  text-decoration: none;
}

.navigation-img {
  position: absolute;
  top: 0px;
  max-width: 1170px;
  z-index: -1;
}

a.ssk.ssk-icon {
  padding-right: 10px;
  padding-left: 10px;
}

.risq-brand {
  margin: 0 auto;
  width: 71%;
  display: block;
  padding-top: 120px;
}

.ssk:before {
  color: #fff !important;
}

.ssk.ssk-lg:before {
  font-size: 50px !important;
}

.risq-social-icons.ssk-lg {
  margin: 0 auto;
  position: relative;
  display: block;
  right: 0px;
  text-align: center;
}

.footer-container {
  background-color: rgba(239, 17, 0, 0.9);
  min-height: 80px;
  padding: 20px;
  color: #fff;
  font-size: 20px;
}

.social-icons {
  padding-top: 20px;
}

a#bottom {
  color: #fff;
}

a#bottom :hover {
  text-decoration: none;
  color: #fff;
}

@media screen and (max-width: 1024px) {
  .risq-nav {
    position: relative;
    height: 80px;
    border-radius: 0 0 0 0;
    width: 100%;
  }
  .navigation-img {
    position: relative;
  }
  .risq-social-icons.ssk-lg {
    display: none;
  }
  .risq-brand {
    padding-top: 0;
    text-align: center;
  }
  .risq-brand img {
    width: 100px;
    margin: 0 auto;
  }
}

