  background-color: rgba(255, 255, 255, 0.3);
}

.btn-ecom {
  text-transform: uppercase;
  border: solid 10px #fff;
  background: rgba(255, 255, 255, 0.65);
  padding: 25px 21px 7px;
  font-family: "eloquent-jf-small-caps-pro" !important;
  font-size: 50px;
