  line-height: 32px;
}

.btn-qty-ecom:hover {
  text-decoration: none;
  color: #fff;
}

.btn-round {
  border: solid 3px #000;
  border-radius: 50px;
  height: 40px;
  width: 40px;
  vertical-align: center;
}

.btn-underline {
  background: rgba(255, 255, 255, 0);
  text-decoration: underline;
  color: #fff;
  border: none;
  font-size: 50px;
}

@media screen and (max-width: 768px) {
  .btn-ecom {
    text-transform: uppercase;
