}

.recipe-bg-image {
  background-image: url("../img/risq-energy-drink-image.jpg");
  height: 700px;
  background-position: center;
  overflow: hidden;
}

.rotate {
  width: 1150px;
  position: relative;
  display: block;
  -moz-transform: rotate(-80deg);
  -o-transform: rotate(-80deg);
  -webkit-transform: rotate(-80deg);
  -ms-transform: rotate(-80deg);
  transform: rotate(-80deg);
}

.recipe-content {
  padding: 20px;
  z-index: 2;
  position: absolute;
  width: 100%;
  height: 702px;
  top: 0;
}

.strawberrykiwi.rotate,
.peachmango.rotate,
.watermelon.rotate,
.greenapple.rotate {
  padding: 30px;
}

.strawberrykiwi.rotate h3, .strawberrykiwi.rotate a,
.peachmango.rotate h3,
.peachmango.rotate a,
.watermelon.rotate h3,
.watermelon.rotate a,
.greenapple.rotate h3,
.greenapple.rotate a {
  text-align: left;
  font-size: 97px;
  padding-left: 80px;
  position: relative;
  display: block;
  text-transform: uppercase;
  color: #fff;
}

.strawberrykiwi.rotate h3 :hover, .strawberrykiwi.rotate h3 :focus, .strawberrykiwi.rotate h3 :visited, .strawberrykiwi.rotate a :hover, .strawberrykiwi.rotate a :focus, .strawberrykiwi.rotate a :visited,
.peachmango.rotate h3 :hover,
.peachmango.rotate h3 :focus,
.peachmango.rotate h3 :visited,
.peachmango.rotate a :hover,
.peachmango.rotate a :focus,
.peachmango.rotate a :visited,
.watermelon.rotate h3 :hover,
.watermelon.rotate h3 :focus,
.watermelon.rotate h3 :visited,
.watermelon.rotate a :hover,
.watermelon.rotate a :focus,
.watermelon.rotate a :visited,
.greenapple.rotate h3 :hover,
.greenapple.rotate h3 :focus,
.greenapple.rotate h3 :visited,
.greenapple.rotate a :hover,
.greenapple.rotate a :focus,
.greenapple.rotate a :visited {
  color: #fff;
  text-decoration: none;
}

.strawberrykiwi.rotate {
  left: -406px;
  bottom: -200px;
}

.peachmango.rotate {
  left: -206px;
  bottom: 4px;
}

.watermelon.rotate {
  left: -7px;
  bottom: 203px;
}

.greenapple.rotate {
  left: 190px;
  bottom: 407px;
}

.strawberrykiwi {
  background-color: #EF1100;
}

.peachmango {
  background-color: #EF4625;
}

.watermelon {
  background-color: #F51841;
}

.greenapple {
  background-color: #329D00;
}

.space {
  padding-top: 10px;
  padding-bottom: 10px;
  display: block;
  position: inherit;
}

#recipe, #recipe2, #recipe3, #recipe4 {
  left: -9999px;
  -webkit-transition: left 0.5s ease;
  transition: left 0.5s ease;
}

.recipe-title {
  left: -406px;
  text-transform: uppercase;
}

.location-space {
  padding-bottom: 15px;
}

@media screen and (max-width: 768px) {
  .rotate {
    width: 100%;
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  .strawberrykiwi.rotate,
  .peachmango.rotate,
  .watermelon.rotate,
  .greenapple.rotate {
    left: 0px;
    bottom: 0px;
    position: inherit;
  }
  .strawberrykiwi.rotate h3,
  .peachmango.rotate h3,
  .watermelon.rotate h3,
  .greenapple.rotate h3 {
    padding-left: 0px;
  }
  .strawberrykiwi.rotate a,
  .peachmango.rotate a,
  .watermelon.rotate a,
  .greenapple.rotate a {
    padding-left: 0px  !important;
    font-size: 37px;
  }
  h3.rotate.recipe-title {
    font-size: 37px;
    left: 0px;
    margin-top: 50px;
  }
  .recipe-bg-image {
    background-image: none;
    height: 100%;
    position: relative;
  }
  .recipe-content {
    height: 100%;
  }
  p.recipe-info {
    left: 15px;
    display: block;
    font-size: 22px;
    width: 92%;
  }
  .location-space {
    display: none;
  }
  .recipe-title {
    left: 0px;
    text-transform: uppercase;
  }
}

